import { Component, OnInit } from '@angular/core';
import { FilterService, FilterTag } from 'src/app/service/filter.service';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-filter-tags-scroller',
  templateUrl: './filter-tags-scroller.component.html',
  styleUrls: ['./filter-tags-scroller.component.css']
})
export class FilterTagsScrollerComponent implements OnInit {
  constructor(private _filter:FilterService) { }

  filterTagsList$: Observable<FilterTag[]>;
  filterTagsList: FilterTag[];

  ngOnInit(): void {
    this.filterTagsList$ = this._filter.getFilterTagsList();
    this.filterTagsList$.subscribe(res=>{
      this.filterTagsList = res;
    });
  }

  remove(tagId: number): void {
    this._filter.removeTag(tagId);
  }

}
