import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { MatToolbarModule,
  MatIconModule,
  MatCardModule,
  MatButtonModule,
  MatProgressSpinnerModule,
  MatInputModule,
  MatAutocompleteModule,
  MatTooltipModule,
  MatSidenavModule,
  MatRadioModule,
  MatCheckboxModule,
  MatExpansionModule,
  MatChipsModule } from '@angular/material';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';

import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule } from '@angular/router';
import { MeanContentComponent } from './component/mean-content/mean-content.component';
import { HeaderComponent } from './component/header/header.component';
import { TagPipe } from './pipe/tag.pipe';
import { FilterInputComponent } from './component/header/filter-input/filter-input.component';
import { SectionComponent } from './component/meanContent/section/section.component';
import { DishesComponent } from './component/meanContent/section/dishes/dishes.component';
import { IngredientChipComponent } from './component/ingredient-chip/ingredient-chip.component';
import { FilterTagsScrollerComponent } from './component/header/filter-tags-scroller/filter-tags-scroller.component';

// AoT requires an exported function for factories
export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http);
}

@NgModule({
  declarations: [
    AppComponent,
    MeanContentComponent,
    HeaderComponent,
    TagPipe,
    FilterInputComponent,
    SectionComponent,
    DishesComponent,
    IngredientChipComponent,
    FilterTagsScrollerComponent
    ],
  imports: [
    BrowserModule,
    HttpClientModule,
    BrowserAnimationsModule,
    MatToolbarModule,
    MatIconModule,
    MatButtonModule,
    MatCardModule,
    MatProgressSpinnerModule,
    RouterModule.forRoot([]),
    MatInputModule,
    MatAutocompleteModule,
    MatTooltipModule,
    MatSidenavModule,
    MatRadioModule,
    MatCheckboxModule,
    FormsModule,
    ReactiveFormsModule,
    MatExpansionModule,
    MatChipsModule,
    TranslateModule.forRoot({
      loader: {
          provide: TranslateLoader,
          useFactory: HttpLoaderFactory,
          deps: [HttpClient]
      }
  })
],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
