import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Ingredient } from '../model/ingredient.model';
import { Subject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ApiService {
  apiURL: string = 'https://api.airtable.com/v0/appCOzslUwCkgew0E/';
  API_KEY: string = 'keyCiWnxJkhPKys5Z';


  constructor(private httpClient: HttpClient) {}

  private getAirTableWithIdApi(tableName: string){
    let response$ = new Subject<any>();

    let params = new HttpParams().set('api_key', this.API_KEY);
    this.httpClient.get(`${this.apiURL}`+tableName,{responseType:"json", params})
     .subscribe((data: any) => {
        response$.next(data);
      });

    return response$.asObservable();
  }
  private getApi(tableName: string,offset: string, response: string[],responseList$,moreParams?: HttpParams){
    let params = new HttpParams().set('api_key', this.API_KEY).set('offset',offset);
  
    this.httpClient.get(`${this.apiURL}`+tableName,{responseType:"json", params})
     .subscribe((data: AirtableResponseUnit) => {
        response = [...response, ...data.records];
        params.set('offset',data.offset);
          if (data.offset && data.offset != '0' ) {
            this.getApi(tableName,data.offset,response,responseList$,moreParams);
          }
          else{
            responseList$.next(response);
          }
      });
    }
    
  public getNutritionsRequest(){
    let responseList$ = new Subject<string[]>();
    this.getApi('Nutritions','0',[],responseList$);
    return responseList$.asObservable();
  }
  public getIngredientsRequest(){
    let responseList$ = new Subject<string[]>();
    this.getApi('Ingredients','0',[],responseList$);
    return responseList$.asObservable();
  }

  public getMenuDishesRequest(menuId: string){
    return this.getAirTableWithIdApi('Menus/'+menuId);
    // let responseList$ = new Subject<string[]>();
    // this.getApi('Dishes/'+menuId,'0',[],responseList$);
    // return responseList$.asObservable();
  }
  public getDishRequest(dishId: string){
    return this.getAirTableWithIdApi('Dishes/'+dishId);
    // let responseList$ = new Subject<string[]>();
    // this.getApi('Dishes/'+menuId,'0',[],responseList$);
    // return responseList$.asObservable();
  }
}

export interface AirtableResponseUnit {
  records;
  offset;
}
