import { Ingredient } from './ingredient.model';

export class Dish {
    public id: string;
    public name: string;
    public specifications: string;
    public servingStartTime: string;
    public servingEndTime: string;
    public info: string;
    public description: string;
    public price: number;
    public requiredIngredients: Ingredient[];
    public optionalIngredients: Ingredient[];
    public isShowing: boolean;
    public isForbidden: boolean;
    public relevantIngredients: Ingredient[];
    public relevantRequiredTypes: number[];
    public relevantOptionalTypes: number[];

    
    constructor(id:string){
        this.id = id;
        this.requiredIngredients = [];
        this.optionalIngredients = [];
        this.isShowing = true;
        this.isForbidden = false;
        this.relevantRequiredTypes = [];
        this.relevantOptionalTypes = [];
    }
    
}