import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs';
import { ApiService } from './api.service';
import { Nutrition } from '../model/nutrition';

@Injectable({
  providedIn: 'root'
})
export class NutritionService {

  nutritions$ = new Subject<any[]>();
  nutritions: Nutrition[]= [];

  constructor(private _apiService: ApiService) { 
    this.initNutritions();
  }

  getNutrition(): Observable<Nutrition[]>{
    return this.nutritions$;
  }
  initNutritions(){
    this._apiService.getNutritionsRequest().subscribe((nutritions :any[])=>{
      for (let i = 0; i < nutritions.length; i++) {
        let nutrition = new Nutrition(nutritions[i].id);
        if( nutritions[i].fields.Name) nutrition.name = nutritions[i].fields.Name.split("|");
        if(nutritions[i].fields.Forbidden_Category) nutrition.forbiddenCategory = nutritions[i].fields.Forbidden_Category; 
      //if(nutritions[i].fields.Category) nutrition.category = nutritions[i].fields.Category; 
      //if(nutritions[i].fields.Description) nutrition.description = nutritions[i].fields.Description; 
        this.nutritions.push(nutrition);
      }
      //this.ingredients.push(ingredients);
      console.log(this.nutritions);
      this.nutritions$.next(this.nutritions);
    });
  }
  getById(id) {
    let index = this.nutritions.map(e=> { return e.id; }).indexOf(id);
    return this.nutritions[index];
  }
}
